body {
  padding: 10% 10%;

}

table {
  width: 100%;
  border-collapse: collapse;
}

table, th, td {
  border: 1px solid black;
}

.Select {
  width: 80%;
}


img {
  height: 39px;
}

button {
  background-color: #008CBA; /* Green */
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  height: 39px;
}

.login__wrapper {
  width: 100%;
  height: 60px;
}

.login__wrapper > *{
  vertical-align: middle;
  display: inline-block;
}
